import React, { useState } from 'react';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Button from '@material-ui/core/Button';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import ToDoList from './ToDoList';
import Footer from './Footer';

function App() {

  const [inputItems, setInputItems] = useState("");

  const [itemList, setItemList] = useState([]);

  const inputValue = (event) => {
    setInputItems(event.target.value);
  }

  const addItems = () => {
    setItemList((oldItemsValue) => {
      return [...oldItemsValue, inputItems];
    });
    setInputItems("");
  }

  const deleteItem = (id) => {
    setItemList((oldItemsValue) => {
      return oldItemsValue.filter((arrayElement, index) => {
        return index !== id;
      })
    });
  }

  return (
    <>
      <div className="mainDiv">
        <div className="centerDiv w-75">
          <h1 className="text-center my-3 todo-heading">todo-list</h1>
          <br />
          <div className="inputAndBtnStyle">
            <input type="text" value={inputItems} placeholder="Add an item" onChange={inputValue} className="w-50" />
            <Button onClick={addItems}>
              <AddCircleIcon />
            </Button>
          </div>
          <ol>
            {/* <li>{inputItems}</li> */}
            {
              itemList.map((inputItemValue, index) => {
                return (
                  <ToDoList text={inputItemValue} key={index} id={index} onselect={deleteItem} />
                );
              })
            }
          </ol>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default App;
